.tab-content .tab-pane {
  padding: 20px;
}

.consolidated-charts {
  margin: 30px auto 0;
  height: 588px;
}

.carousel-indicators .active {
  background: #286090;
}

.carousel-indicators li {
  border: 1px solid #286090;
}

.datepicker,
.table-condensed {
  font-size: 13px;
  width: 328px;
}

#legends option[selected] {
  color: #3b9de8 !important;
  background-color: #dfe2e3 !important;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 11%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 2px solid #dfe2e3;
  border-top-color: #ed1b24;
  animation: spinner .6s linear infinite;
}

#export_excel {
  margin-top: -7px;
  position: relative;
}

.table {
  font-size: 12px;
}

.open-legends {
  cursor: pointer;
  fill: #3b9de8;
  font-size: 9px;
  font-weight: normal;
}

.search_backdrop {
  background-color: #d8d2d2;
  display: none;
  width: 100%;
  position: absolute;
  height: 100%;
  z-index: 999;
  opacity: 0.1;
  top: 0;
}

.loader {
  display: none;
  margin: auto;
  width: 70px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader > div {
  width: 18px;
  height: 18px;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.loader .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.loader .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

/* Dropdown Button */
.dropbtn {
  background-color: transparent;
  border: none;
  color: #4CAF50;
  cursor: pointer;
  font-size: 30px;
  left: 10px;
  position: absolute;
  top: 30px;
  text-align: left;
}

/* The container <div> - needed to position the dropdown content */
#custom_settings {
  position: relative;
  z-index: 999;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 230px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  top: 64px;
  left: 22px;
}

.dropdown-content .row {
  margin-bottom: 0;
}

.dropdown-content h4 {
  margin: 6px 0;
}

/* Links inside the dropdown */
.dropdown-content ul {
  padding: 0;
}

.dropdown-content li {
  color: black;
  padding: 6px 8px;
  text-decoration: none;
  display: block;
  text-decoration: none;
}

/* Change color of dropdown links on hover */
.dropdown-content li:hover {
  background-color: #f1f1f1;
}

.dropdown-content .form-group {
  margin-bottom: 0;
}

.dropdown-content .btn-group label.btn {
  background: none;
  border: none;
  box-shadow: none;
  padding-top: 0;
  padding-bottom: 0;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

.form-group input[type="checkbox"] {
  display: none;
}

.form-group input[type="checkbox"] + .btn-group > label span {
  width: 20px;
}

.form-group input[type="checkbox"] + .btn-group > label span:first-child {
  display: none;
}

.form-group input[type="checkbox"] + .btn-group > label span:last-child {
  display: inline-block;
}

.form-group input[type="checkbox"]:checked + .btn-group > label span:first-child {
  display: inline-block;
}

.form-group input[type="checkbox"]:checked + .btn-group > label span:last-child {
  display: none;
}

table td {
  text-align: right;
}

.settings_label {
  position: absolute !important;
  text-align: left;
}
